<template>
  <div class="customer-list">
    <h1 class="customer-list__title">Список клиентов</h1>
    <router-link class="btn btn--icon btn--add" to="/create"></router-link>
    <p class="customer-list--empty"
      v-if="customers.length === 0">
      Пока не добавлено ни одного клиента (:
    </p>
    <div class="customer-list__table table" v-for="customer in customers" :key="customer._id">
      <div class="table__row">
        <span class="table__data">{{ customer.last_name }} {{ customer.first_name[0] }}. {{ customer.middle_name [0] }}.</span>
        <!-- to do: hover effect -->
        <!-- <span class="table__data">{{ customer.email }}</span> -->
        <!-- <span class="table__data">{{ customer.phone }}</span> --> 
        <span class="table__data">{{ customer.status }}</span>
        <router-link :to="{
            name: 'View',
            params: {
              id: customer._id
              }
            }"
            class="btn btn--icon btn--view">
            V
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import CustomerService from '@/services/CustomerService'
export default {
  data() {
    return {
      customers: []
    }
  },
  created() {
    this.fetchCustomers();
  },
  methods: {
    fetchCustomers() {
      CustomerService.getAllCustomers()
        .then(data => (this.customers = data.data))
    }
  }
}
</script>

<style lang="scss">
  .customer-list {
    &__title {
      text-align: center;
      color: #fff;
      background: #164697;
      font-size: 1.5rem;
      padding-right: 50px;
    }
  }
</style>
